import ForgotPassword from "./pages/Auth/ForgotPassword";
import Signup from "./pages/Auth/Signup";
import Dashboard from "./pages/Dashboard";
import LandingPage from "./pages/Landing";
import MasterStudies from "./pages/Masterdata/MasterStudies";
import MasterTopic from "./pages/Masterdata/MasterTopics";
import Profile from "./pages/Profile";
import UserEnroll from "./pages/Users/UserEnroll";
import UserRegister from "./pages/Users/UserRegister";
// import SignUp from "./"

const routes = [
  {
    path: "/",
    layout: "landing",
    component: LandingPage,
    basePath: true,
  },
  {
    path: "/about",
    layout: "landing",
    component: LandingPage,
    basePath: true,
  },
  {
    path: "/course",
    layout: "landing",
    component: LandingPage,
    basePath: true,
  },
  {
    path: "/signup",
    layout: "auth",
    component: Signup,
    basePath: true,
  },
  {
    path: "/",
    layout: "admin",
    component: Dashboard,
    basePath: true,
  },
  {
    path: "/profile",
    layout: "admin",
    basePath: true,
    component: Profile,
  },
  {
    path: "/user/register",
    layout: "admin",
    basePath: true,
    component: UserRegister,
  },
  {
    path: "/master/settopic",
    layout: "admin",
    basePath: true,
    component: MasterTopic,
  },
  {
    path: "/master/setstudy",
    layout: "admin",
    basePath: true,
    component: MasterStudies
  },
  {
    path : "/user/enroll",
    layout : "admin",
    basePath : true,
    component : UserEnroll
  },
  {
    path: "/resetpassword",
    layout: "auth",
    component: ForgotPassword,
    basePath: true,
  },
];

export default routes;
