import { Delete, Edit } from "@mui/icons-material";
import { Card, CardMedia, CardContent, IconButton } from "@mui/material";
import React from "react";
import BoxCustom from "../Box";
import Avatardefault from "../../assets/images/avatardefault.png";

const StudyCard = (props) => {
  const { data, onDelete, onUpdate } = props;
  return (
    <Card sx={{ width: "100%" }}>
      <div>
        
      </div>
      <CardContent>
        <BoxCustom>{data.title}</BoxCustom>
        <BoxCustom width="100%" justify="flex-end" direction="row">
          <IconButton onClick={() => onUpdate(data.id)} color="primary">
            <Edit />
          </IconButton>
          <IconButton onClick={() => onDelete(data.id)} color="error">
            <Delete />
          </IconButton>
        </BoxCustom>
      </CardContent>
    </Card>
  );
};

export default StudyCard;
