import { Grid } from "@mui/material";
import React, { Component } from "react";
import styled from "styled-components";
import ButtonCustom from "../../components/Button/Button";
import StudyCard from "../../components/Card/StudyCard";
import Search from "../../components/Form/Search";
import { Api } from "../../services/api";

export default class MasterStudies extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      limit: 10,
      page: 0,
      search: "",
    };
  }

  fetchStudies = () => {
    const { limit, page, search } = this.state;
    Api.post("/master_studies", {
      limit,
      page,
      search,
    })
      .then((response) => {
        if (response.data.code === 200 && response.status === 200) {
          this.setState({ data: response.data.data });
        }
      })
      .catch((error) => alert(error.message));
  };

  componentDidMount = () => {
    this.fetchStudies();
  };
  render() {
    const { data } = this.state;
    console.log(data);
    return (
      <WrapContent>
        <Grid className="head" container>
          <Grid item md={7} xl={7} xs={12}>
            <WrapButton>
              <ButtonCustom>ADD STUDY</ButtonCustom>
              <ButtonCustom>FILTER</ButtonCustom>
            </WrapButton>
          </Grid>
          <Grid item md={5} xl={5} xs={12}>
            <Search width="100%" />
          </Grid>
        </Grid>
        <WrapStudy>
          <Grid
            sx={{ maxHeight: 520, overflowX: "scroll" }}
            container
            spacing={2}
          >
            {data.map((itm, index) => {
              return (
                <Grid key={index} item xl={3} md={3} xs={12}>
                  <StudyCard data={itm} />
                </Grid>
              );
            })}
          </Grid>
        </WrapStudy>
      </WrapContent>
    );
  }
}

const WrapContent = styled.div`
  padding: 17px 15px;
`;

const WrapButton = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 30%;

  @media screen and (max-width: 400px) {
    width: 100%;
  }
`;

const WrapStudy = styled.div`
  margin-top: 15px;
`;
